/* jshint ignore:start */

if (!runningTests) {
  require("ember-progressive-webapp/app")["default"].create({"name":"ember-progressive-webapp","version":"v0.0.4"});
}

define('~fastboot/app-factory', ['ember-progressive-webapp/app', 'ember-progressive-webapp/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
